	module.exports = {"data":{"markdownRemark":{"html":"<h3>Regex alternations and dependency depression</h3>\n<ul>\n<li>Use | as an alternation in regex patterns</li>\n<li>The past few days I have been working through some tutorials, one on React and the other on AngularJs 1.6. While I am making progress, I am not sure what to make of the incredible amount of config and boilerplate one has to setup in order to get going. I understand that modern JS apps have a lot on their plate and one needs to cater to all these different development processes and build processes, but with it seems to me that the same convenience that makes NPM so powerful is the same thing that makes it so tricky to figure out what is happening when something goes wrong. While I have managed to get the projects running, on one occassion there is a NPM registry completely missing. On the other, I have forked a repo directly and run npm install and out of the box, babel/register cannot load... </li>\n</ul>","frontmatter":{"title":"TIL 0015"}}},"pathContext":{"slug":"/til0015/"}}

